
@font-face {
  font-family: 'gw2';
  src: url('./assets/gw.ttf');
}
body {
  font-family: 'Lato', Arial, sans-serif;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.2)), to(rgba(0, 0, 0, 0.2)) ), url('./assets/pattern1.jpg');
  background: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ), url('./assets/pattern1.jpg');
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
}



